.selected-projects {
  padding: 2em 0;
}
.selected-projects .selected-projects-title {
  display: block;
  width: 100%;
  margin: 0 auto;
  font-size: 2.3em;
  line-height: 1.2em;
  text-transform: uppercase;
  text-align: center;
  color: #F3EEC8;
}
.selected-projects .selected-projects-subtitle {
  display: block;
  width: 85%;
  max-width: 490px;
  margin: 0 auto;
  padding: 1em 0 0;
  font-size: 1em;
  line-height: 1.2em;
  text-align: center;
  text-indent: 3%;
  color: #ffffff;
}
.selected-projects .btn-view-allpro {
  display: block;
  width: 170px;
  margin: 80px auto;
  padding: 0.6em 0;
  color: #F3EEC8;
  border: 2px solid #F3EEC8;
  border-radius: 8px;
  text-decoration: none;
}
.selected-projects .btn-view-allpro p {
  font-size: 1em;
}

.selected-projects-list {
  display: block;
  width: 100%;
  max-width: 1080px;
}
.selected-projects-list .selected-project-item {
  display: block;
  width: 100%;
  margin: 30px 0 0;
  position: relative;
  border-radius: 4px;
  overflow: hidden;
}
.selected-projects-list .selected-project-item img {
  max-width: 100%;
  height: auto;
}
.selected-projects-list .selected-project-item a {
  display: block;
  text-decoration: none;
  font-weight: 600;
}
.selected-projects-list .selected-project-item .project-side-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  padding: 1em 0;
  position: absolute;
  bottom: 0%;
  left: 50%;
  transform: translate(-50%, 0%);
  background: linear-gradient(rgba(10, 10, 10, 0), #0A0A0A);
}
.selected-projects-list .selected-project-item .project-side-container h3, .selected-projects-list .selected-project-item .project-side-container p, .selected-projects-list .selected-project-item .project-side-container h5 {
  display: block;
  width: 90%;
  margin: 0 auto;
  text-align: left;
}
.selected-projects-list .selected-project-item .project-side-container h3, .selected-projects-list .selected-project-item .project-side-container p {
  text-shadow: 0 0 6px #0A0A0A;
}
.selected-projects-list .selected-project-item .project-side-container h3 {
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  font-size: 1.6em;
  color: #F3EEC8;
}
.selected-projects-list .selected-project-item .project-side-container p {
  display: none;
  font-size: 0.9em;
  font-weight: 300;
  line-height: 1.3em;
  padding: 1em 0 0;
  color: #ffffff;
}
.selected-projects-list .selected-project-item .project-side-container h5 {
  width: -moz-fit-content;
  width: fit-content;
  margin: 1em 5% 0;
  padding: 1em 1em;
  border-radius: 100px;
  font-size: 0.7em;
  color: #ffffff;
  background: rgba(90, 90, 90, 0.5);
}

@media (orientatioselected-n: landscape) {
