    width: 65%;
  }
}
@media (min-width: 768px) {
  .selected-projects .selected-projects-title {
    font-size: 6em;
    padding: 0.3em 0 0.2em;
  }
  .selected-projects .btn-view-allpro {
    margin: 100px auto;
  }
  .selected-projects-list {
